import { Component, OnInit } from '@angular/core';
import { NewsService } from '../../../services/news.service';
import { NewsModel } from '../../../models/news.model';
import { Subscription } from 'rxjs';
import { ModalImagenService } from '../../../services/modal-imagen.service';
import { delay } from 'rxjs/operators';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-noticias',
  templateUrl: './noticias.component.html',
  styles: [
  ]
})
export class NoticiasComponent implements OnInit {

  public noticias:NewsModel[]=[];
  public totalNews: number = 0;
  public cargando : boolean = true;
  public desde: number = 0;
  public imgSubs: Subscription;

  constructor(private newsService:NewsService,
              private modalImagenService:ModalImagenService, ) {
    
  }

  ngOnDestroy(): void {
    this.imgSubs.unsubscribe();
  }

  ngOnInit(): void {
    this.obtenerNews();
    this.imgSubs = this.modalImagenService.nuevaImagen
      .pipe(delay(100))
      .subscribe( img => this.obtenerNews() );
  }
 
  obtenerNews(){
  this.cargando=true;
   this.newsService.cargarNoticePagination(this.desde)
   .subscribe( ({total ,news})=>{  
     this.noticias = news
     this.totalNews = total;
     this.cargando=false;
   });
  }

  cambiarPagina( valor :number){
    this.desde += valor;
    if (this.desde < 0){
      this.desde = 0
    }else if (this.desde > this.totalNews){
      this.desde -= valor;
    }
    this.obtenerNews();
  }
  

  abrirModal(news:NewsModel){
    this.modalImagenService.abrirModal( 'news', news._id, news.img );
   }

   
 borrarImage( news:NewsModel ) {

  Swal.fire({
    title: '¿Borrar Imagen?',
    text: `Esta a punto de borrar a ${ news.title}`,
    icon: 'question',
    showCancelButton: true,
    confirmButtonText: 'Si, borrarlo'
  }).then((result) => {
    if (result.value) {
      
      this.newsService.deleteNew( news._id )
        .subscribe( resp => {
          
          this.obtenerNews();
          Swal.fire(
            'Foto borrada',
            `${ news.title } fue eliminado correctamente`,
            'success'
          );
          
        });

    }
  })

}
 
  
  
 



}
