import { Component, OnInit } from '@angular/core';
import { ModalImagenService } from '../../../services/modal-imagen.service';
import Swal from 'sweetalert2';
import { SectionService } from '../../../services/section.service';
import { Section } from '../../../models/section.model';
import { Subscription } from 'rxjs';
import { delay } from 'rxjs/operators';


@Component({
  selector: 'app-blog',
  templateUrl: './blog.component.html',
  styles: [
  ]
})
export class BlogComponent implements OnInit {

  public totalArticles: number = 0;
  public cargando : boolean = true;
  public sections :Section[]=[];
  public desde: number = 0;
  public imgSubs: Subscription;
 
  constructor(private sectionService:SectionService,
              private modalImagenService:ModalImagenService,
              ){}
 
 
  ngOnDestroy(): void {
  this.imgSubs.unsubscribe();
  }


  ngOnInit():void{
    this.cargarSections();
    this.imgSubs = this.modalImagenService.nuevaImagen
    .pipe(delay(100))
    .subscribe( img => this.cargarSections() );
  }



 cargarSections(){
  this.cargando = true;
  this.sectionService.cargarArticlesPagination(this.desde)
  .subscribe( ({total , sections}) =>{
    this.cargando=false;
    this.sections=sections;
    this.totalArticles=total;
    console.log(sections)
  });
 }

 abrirModal(sections:Section){
  this.modalImagenService.abrirModal( 'sections', sections._id, sections.img );
 }



 cambiarPagina( valor :number){
  this.desde += valor;
  if (this.desde < 0){
    this.desde = 0
  }else if (this.desde > this.totalArticles){
    this.desde -= valor;
  }
  this.cargarSections();
}


 borrarImage( section:Section ) {

  Swal.fire({
    title: '¿Borrar Articulo?',
    text: `Esta a punto de borrar a ${ section.title}`,
    icon: 'question',
    showCancelButton: true,
    confirmButtonText: 'Si, borrarlo'
  }).then((result) => {
    if (result.value) {
      
      this.sectionService.borrarSections( section._id )
        .subscribe( resp => {
          
          this.cargarSections();
          Swal.fire(
            'Foto borrada',
            `${ section.title } fue eliminado correctamente`,
            'success'
          );
          
        });

    }
  })

}

}
