import { Component, OnInit } from '@angular/core';
import {  FormGroup, FormBuilder, Validators } from '@angular/forms';
import { SectionService } from '../../../services/section.service';
import { Router, ActivatedRoute } from '@angular/router';
import Swal from 'sweetalert2';
import { Section } from 'src/app/models/section.model';

@Component({
  selector: 'app-blogs',
  templateUrl: './blogs.component.html',
  styles: []
})
export class BlogsComponent implements OnInit {

  public sectionForm:FormGroup;
  public sectionSeleccionado:Section;

  constructor(private fb:FormBuilder,
              private sectionService: SectionService,
              private router:Router,
              private activatedRoute:ActivatedRoute,
              ) { }

  ngOnInit(): void {
  this.activatedRoute.params.
    subscribe( ({id} ) => this.cargarSection(id) );

  this.sectionForm=this.fb.group({
          title:['', Validators.required],
          description:['', Validators.required],          
          autor:['', Validators.required],          
  });
  }
  cargarSection(id:string){
    if( id === 'new'){
      return;
    }
     this.sectionService.obtenerSectionsPorId(id)
     .subscribe((section:any)=>{
       if(!section){
         return this.router.navigateByUrl(`/dashboard/photos`);
       }
       const {title, description,autor} = section;
       this.sectionSeleccionado = section ;
       this.sectionForm.setValue({title,description,autor});
     })
   }
 

  guardarSection() {

    const {title}=this.sectionForm.value;

    if ( this.sectionSeleccionado) {
      // actualizar
      const data = {
        ...this.sectionForm.value,
        _id: this.sectionSeleccionado._id
      }
      this.sectionService.actualizarSections( data )
        .subscribe( resp => {
          Swal.fire('Actualizado', `${ title } actualizado correctamente`, 'success')
          this.router.navigateByUrl(`/dashboard/blog`)
        })

    } else {
      // crear
      this.sectionService.crearSection(this.sectionForm.value)
      .subscribe((resp:any)=>{
        console.log(resp);
        Swal.fire('Creado', `${title} creado correctamente`, 'success')
        this.router.navigateByUrl(`/dashboard/blog`)
      })
    }
    

  }
}
