import { Component, OnInit } from '@angular/core';
import Swal from 'sweetalert2';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { QuestionModel } from 'src/app/models/question.model';
import { QuestionService } from '../../../services/question.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-question',
  templateUrl: './question.component.html',
  styles: ['']
})
export class QuestionComponent implements OnInit {

  public sectionForm:FormGroup;
  public questionSelecionado:QuestionModel;

  constructor(private fb:FormBuilder,
              private qestionService: QuestionService,
              private router:Router,
              private activatedRoute:ActivatedRoute,
              ) { }

  ngOnInit(): void {
  this.activatedRoute.params.
    subscribe( ({id} ) => this.cargarSection(id) );

  this.sectionForm=this.fb.group({
          title:['', Validators.required],
          description:['', Validators.required],                 
          enlace:['', Validators.required],          
  });
  }
  cargarSection(id:string){
    if( id === 'new'){
      return;
    }
     this.qestionService.obtenerQuestionPorId(id)
     .subscribe((question:any)=>{
       if(!question){
         return this.router.navigateByUrl(`/dashboard/photos`);
       }
       const {title, description,enlace} = question;
       this.questionSelecionado = question ;
       this.sectionForm.setValue({title,description,enlace});
     })
   }
 

  guardarSection() {

    const {title}=this.sectionForm.value;

    if ( this.questionSelecionado) {
      // actualizar
      const data = {
        ...this.sectionForm.value,
        _id: this.questionSelecionado._id
      }
      this.qestionService.actualizarQuestion( data )
        .subscribe( resp => {
          Swal.fire('Actualizado', `${ title } actualizado correctamente`, 'success')
          this.router.navigateByUrl(`/dashboard/questions`)
        })

    } else {
      // crear
      this.qestionService.crearQuestion (this.sectionForm.value)
      .subscribe((resp:any)=>{
        console.log(resp);
        Swal.fire('Creado', `${title} creado correctamente`, 'success')
        this.router.navigateByUrl(`/dashboard/questions`)
      })
    }
    

  }
}

