import { Injectable } from '@angular/core';
import { environment } from '../../environments/environment';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { QuestionModel } from '../models/question.model';
import { CargarQuestion } from '../interfaces/cargar-question.interface';

const base_url = environment.base_url
@Injectable({
  providedIn: 'root'
})
export class QuestionService {

  constructor(private http:HttpClient) { }
  get token(): string {
    return localStorage.getItem('token') || '';
  }
  
  get headers() {
    return {
      headers: {
        'x-token': this.token
               }
          }
  }
  
  getQuestion(){
    const url = (base_url+`/question`)
    return this.http.get( url, this.headers )
    .pipe(
      map( (resp: {ok: boolean, question: QuestionModel }) => resp.question )
    );
   }

   obtenerQuestionPorId(id:string) {
    const url = `${ base_url }/question/${id}`;
    return this.http.get( url, this.headers )
              .pipe(
                map( (resp: {ok: boolean,  question: QuestionModel[] }) => resp.question )
              );
  }
  
  
  crearQuestion(question:{title:string, description:string, date:Date ,autor:string}) {
  
    const url = `${ base_url }/question`;
    return this.http.post( url, question , this.headers );
  }
  
  actualizarQuestion( question:QuestionModel ) {
  
    const url = `${ base_url }/question/${ question._id }`;
    return this.http.put( url, question, this.headers );
  }
  borrarQuestion( _id: string ) {
  
    const url = `${ base_url }/question/${ _id }`;
    return this.http.delete( url, this.headers );
  }

  cargarArticlesPagination(page:number= 0) {
    const url = `${ base_url }/question?desde=5&page=${page}`;
    return this.http.get<CargarQuestion>(url)
  }
  
  
}
