import { Injectable } from '@angular/core';
import { environment } from '../../environments/environment';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { SchoolModel } from '../models/school.model';




const base_url=environment.base_url;
@Injectable({
  providedIn: 'root'
})
export class SchoolService {
 
  public school:SchoolModel;
  

  constructor(private http:HttpClient){}
  get token(): string {
    return localStorage.getItem('token') || '';
  }

  get headers() {
    return {
      headers: {
        'x-token': this.token
      }
    }
  }
 // cargarSchool() {
  //  const url = `${ base_url }/school/60da30ded991fe468c3060af`;
  //  return this.http.get( url, this.headers )
  //            .pipe(
  //              map( (resp: {ok: boolean, school: SchoolModel[] }) => resp.school )
  //            );
  //}
  cargarSchoolById(id="60da30ded991fe468c3060af", data="") {
    const url = `${ base_url }/school/${id}`;
    return this.http.get( url, this.headers )
              .pipe(
                map( (resp: {ok: boolean, school: SchoolModel[] }) => resp.school )
              );
  }
  CrearSchool(shool:{title:string, description:string, date:Date}) {
    const url = `${ base_url }/school`;
    return this.http.post( url,shool,this.headers )
              .pipe(
                map( (resp: {ok: boolean, school: SchoolModel[]}) => resp.school )
              );
  }

  actualizarSchool(school:SchoolModel) {
    const url = `${ base_url }/school/${school._id}`;
    return this.http.put( url,school,this.headers )
              .pipe(
                map( (resp: {ok: boolean, school: SchoolModel[]}) => resp.school )
              );
  }


}








