import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../environments/environment.prod';
import { map, tap } from 'rxjs/operators';
import { Item } from '../models/galeria.model';
import { CargarFotos } from '../interfaces/cargar-items.interface';

const base_url=environment.base_url;

@Injectable({
  providedIn: 'root'
})
export class ItemsService {

  public item: Item;

  constructor(private http:HttpClient){}
get token(): string {
  return localStorage.getItem('token') || '';
}

get headers() {
  return {
    headers: {
      'x-token': this.token
    }
  }
}

cargarItems() {

  const url = `${ base_url }/item`;
  return this.http.get( url, this.headers )
            .pipe(
              map( (resp: {ok: boolean, items: Item[] }) => resp.items )
            );
}
obtenerItemsPorId(id:string) {

  const url = `${ base_url }/item/${id}`;
  return this.http.get( url, this.headers )
            .pipe(
              map( (resp: {ok: boolean, items: Item }) => resp.items )
            );
}
crearItems( item:{title:string, description:string, autor:string}) {

  const url = `${ base_url }/item`;
  return this.http.post( url, item , this.headers );
}

actualizarItem( item: Item  ) {

  const url = `${ base_url }/item/${ item._id }`;
  return this.http.put( url, item, this.headers );
}



borrarItems( _id: string ) {

  const url = `${ base_url }/item/${ _id }`;
  return this.http.delete( url, this.headers );
}

cargarArticlesPagination(page:number= 0) {
  const url = `${ base_url }/item?desde=5&page=${page}`;
  return this.http.get<CargarFotos>(url)
}
  
}


