import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { NewsModel } from 'src/app/models/news.model';
import { NewsService } from '../../../services/news.service';
import { Router, ActivatedRoute } from '@angular/router';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-noticia',
  templateUrl: './noticia.component.html',
  styles: [
  ]
})
export class NoticiaComponent implements OnInit {

  public sectionForm:FormGroup;
  public newsSelecionado:NewsModel;

  constructor(private fb:FormBuilder,
              private newsService:NewsService,
              private router:Router,
              private activatedRoute:ActivatedRoute,
              ) { }

  ngOnInit(): void {
  this.activatedRoute.params.
    subscribe( ({id} ) => this.cargarSection(id) );

  this.sectionForm=this.fb.group({
          title:['', Validators.required],
          description:['', Validators.required],                 
          autor:['', Validators.required],                 
                   
  });
  }

  cargarSection(id:string){
    if( id === 'new'){
      return;
    }
     this. newsService.getNewsPorId(id)
     .subscribe((news:any)=>{
       if(!news){
         return this.router.navigateByUrl(`/dashboard/news`);
       }
       const {title, description,autor} = news;
       this.newsSelecionado = news ;
       this.sectionForm.setValue({title,description,autor});
     })
   }
 

  guardarSection() {

    const {title}=this.sectionForm.value;

    if ( this.newsSelecionado) {
      // actualizar
      const data = {
        ...this.sectionForm.value,
        _id: this.newsSelecionado._id
      }
      this.newsService.purNew( data )
        .subscribe( resp => {
          Swal.fire('Actualizado', `${ title } actualizado correctamente`, 'success')
          this.router.navigateByUrl(`/dashboard/news`)
        })

    } else {
      // crear
      this.newsService.postNew (this.sectionForm.value)
      .subscribe((resp:any)=>{
        console.log(resp);
        Swal.fire('Creado', `${title} creado correctamente`, 'success')
        this.router.navigateByUrl(`/dashboard/news`)
      })
    }
    

  }
}

