import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { PasswordService } from '../../services/password.service';
import Swal from 'sweetalert2';
import { Router, ActivatedRoute } from '@angular/router';

 
 
@Component({
  selector: 'app-resetpassword',
  templateUrl: './resetpassword.component.html',
  styleUrls: [ './login.component.css' ]
})
export class ResetpasswordComponent implements OnInit {
  
  public formSubmitted = false;
  public token : string;

  
  public forma = this.fb.group({
    token: ['', Validators.required ],
    password: ['', Validators.required ],
    password2: ['', Validators.required ],
    terminos: [ true, Validators.required ],
  }, {
    validators: this.passwordsIguales('password', 'password2'),
  }
  );

  constructor(public passwordService: PasswordService,
              private fb: FormBuilder,
              public router:Router,
              public activatedRoute:ActivatedRoute,) {
  }
 
  ngOnInit() {
    this.activatedRoute.params.subscribe( token => {
      console.log( token['token'] );
      this.token = token['token'];
    });
  }


  resetPassword() {
    this.formSubmitted = true;
    console.log( this.forma.value );

    if ( this.forma.invalid ) {
      return;
    }else{
      let password = this.forma.value.password;
      let token = this.forma.value.token;
      this.passwordService.reestablecerContraseña(password, token)
      .subscribe((resp: any) => {
        resp = resp;
        Swal.fire({
          icon: 'success',
          title: 'Actualizada',
          text: 'Contraseña cambiada correctamente',
        }) 
        this.router.navigateByUrl(`/login`)
      },(err)=>{
       console.log(err)
       Swal.fire('Error', err.error.msg , 'error' );
      });
    }

}



  

contrasenasNoValidas() {
  const pass1 = this.forma.get('password').value;
  const pass2 = this.forma.get('password2').value;

  if ( (pass1 !== pass2) && this.formSubmitted ) {
    return true;
  } else {
    return false;
  }

}


passwordsIguales(pass1Name: string, pass2Name: string ) {

    return ( formGroup: FormGroup ) => {

      const pass1Control = formGroup.get(pass1Name);
      const pass2Control = formGroup.get(pass2Name);

      if ( pass1Control.value === pass2Control.value ) {
        pass2Control.setErrors(null)
      } else {
        pass2Control.setErrors({ noEsIgual: true })
      }


    }
  }


}
 