 const base_url = environment.base_url
@Injectable({
  providedIn: 'root'
})
export class PasswordService {
 
  constructor(public http: HttpClient) { }
 


  get token(): string {
  const token = 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJ0byI6ImFkcmlhbnF1aXNwZTE0MkBnbWFpbC5jb20iLCJpYXQiOjE2MjIyMzI3OTEsImV4cCI6MTYyMjIzNDU5MX0.bGi-2tUeD9oNstNVXsQebnlVmbZOHcTfmxG9rXnNxG0'
    return token
  }
  get headers() {
    return {
      headers: {
        'x-token': this.token
      }
    }
  }


  reestablecerContraseña( password: String, token: String){
   
    let url = base_url+ '/resetpass';
    url += '?x-token=' + token;
    
    return this.http.put(url, { password } )
      .pipe(
        map(
          (resp: any) => {
            console.log(resp.ok) 
            return resp.ok;
          }
        ));

  }

}