import { Injectable } from '@angular/core';
import { environment } from '../../environments/environment.prod';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { NewsModel } from '../models/news.model';
import { CargarNoticias } from '../interfaces/cargar-news.interface';
const base_url = environment.base_url
@Injectable({
  providedIn: 'root'
})
export class NewsService {

  constructor( private http: HttpClient ) { }

  get token(): string {
    return localStorage.getItem('token') || '';
  }

  get headers() {
    return {
      headers: {
        'x-token': this.token
      }
    }
  }


  getNews() {
    const url = `${ base_url }/news`;
    return this.http.get( url, this.headers )
              .pipe(
                map( (resp: {ok: boolean, news: NewsModel[] }) => resp.news )
              );
  }

  getNewsPorId( id: string ) {

    const url = `${ base_url }/news/${ id }`;
    return this.http.get( url, this.headers )
              .pipe(
                map( (resp: {ok: boolean, news: NewsModel }) => resp.news )
              );
  }

  postNew( news: { nombre: string, hospital: string } ) {

    const url = `${ base_url }/news`;
    return this.http.post( url, news, this.headers );
  }
  
  purNew( news: NewsModel  ) {

    const url = `${ base_url }/news/${ news._id }`;
    return this.http.put( url, news, this.headers );
  }

  deleteNew( _id: string ) {
    const url = `${ base_url }/news/${ _id }`;
    return this.http.delete( url, this.headers );
  }
  cargarNoticePagination(page:number= 0) {
    const url = `${ base_url }/news?desde=5&page=${page}`;
    return this.http.get<CargarNoticias>(url)
  }
  
}
