import { Component, OnInit } from '@angular/core';
import { ItemsService } from 'src/app/services/items.service';
import { Item } from '../../../models/galeria.model';
import { ModalImagenService } from '../../../services/modal-imagen.service';
import Swal from 'sweetalert2';
import { delay } from 'rxjs/operators';
import { Subscription } from 'rxjs';
import { BusquedasService } from '../../../services/busquedas.service';

@Component({
  selector: 'app-fotos',
  templateUrl: './fotos.component.html',
  styles: [
  ]
})
export class FotosComponent implements OnInit {
    
    public totalItems: number = 0;
    public cargando : boolean = true;
    public items :Item[]=[];
    public desde: number = 0;
    public imgSubs: Subscription;
 
  constructor(private itemService:ItemsService,
              private modalImagenService:ModalImagenService,
              private busquedasService: BusquedasService
              ){}
  ngOnDestroy(): void {
  this.imgSubs.unsubscribe();
  }
  ngOnInit():void{
    this.cargarItems();
    this.imgSubs = this.modalImagenService.nuevaImagen
    .pipe(delay(100))
    .subscribe( img => this.cargarItems() );
  }

 cargarItems(){
  this.cargando = true;
  this.itemService.cargarArticlesPagination(this.desde)
  .subscribe( ({ total, items }) => {
    this.cargando=false;
    this.totalItems=total;
    console.log(items)
    this.items=items;
    })
 }


 abrirModal(item:Item){
  this.modalImagenService.abrirModal( 'items', item._id, item.img );
 }


 cambiarPagina( valor :number){
  this.desde += valor;
  if (this.desde < 0){
    this.desde = 0
  }else if (this.desde > this.totalItems){
    this.desde -= valor;
  }
  this.cargarItems();
}

 borrarImage( item: Item ) {

  Swal.fire({
    title: '¿Borrar Foto?',
    text: `Esta a punto de borrar a ${ item.title}`,
    icon: 'question',
    showCancelButton: true,
    confirmButtonText: 'Si, borrarlo'
  }).then((result) => {
    if (result.value) {
      
      this.itemService.borrarItems( item._id )
        .subscribe( resp => {
          
          this.cargarItems();
          Swal.fire(
            'Foto borrada',
            `${ item.title } fue eliminado correctamente`,
            'success'
          );
          
        });

    }
  })

}

}
