import { Component, OnInit } from '@angular/core';
import { QuestionModel } from 'src/app/models/question.model';
import { QuestionService } from '../../../services/question.service';
import { ModalImagenService } from '../../../services/modal-imagen.service';
import { Subscription } from 'rxjs';
import { delay } from 'rxjs/operators';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-questions',
  templateUrl: './questions.component.html',
  styles: ['']
})
export class QuestionsComponent implements OnInit {

  public totalQuestions: number = 0;
  public cargando : boolean = true;
  public desde: number = 0;
  public imgSubs: Subscription;
  public questions:QuestionModel[]=[];
  constructor(private questionService:QuestionService,
            private modalImagenService:ModalImagenService) { }

 
  ngOnDestroy(): void {
  this.imgSubs.unsubscribe();
  }


  ngOnInit():void{
    this.getData();
    this.imgSubs = this.modalImagenService.nuevaImagen
    .pipe(delay(100))
    .subscribe( img => this.getData() );
  }

   
  getData(){
    this.cargando = true;
    this.questionService.cargarArticlesPagination(this.desde)
     .subscribe( ({total , question})=>{
      this.cargando=false;
      this.questions=question;
      this.totalQuestions=total;
     })
  }


  cambiarPagina( valor :number){
    this.desde += valor;
    if (this.desde < 0){
      this.desde = 0
    }else if (this.desde > this.totalQuestions){
      this.desde -= valor;
    }
    this.getData();
  }
  


 abrirModal(question:QuestionModel){
  this.modalImagenService.abrirModal( 'question', question._id, question.img );
 }



 borrarImage( question:QuestionModel ) {

  Swal.fire({
    title: '¿Borrar Imagen?',
    text: `Esta a punto de borrar a ${ question.title}`,
    icon: 'question',
    showCancelButton: true,
    confirmButtonText: 'Si, borrarlo'
  }).then((result) => {
    if (result.value) {
      
      this.questionService.borrarQuestion( question._id )
        .subscribe( resp => {
          
          this.getData();
          Swal.fire(
            'Foto borrada',
            `${ question.title } fue eliminado correctamente`,
            'success'
          );
          
        });

    }
  })

}

}

