import { Injectable } from '@angular/core';
import { environment } from '../../environments/environment';
import { Section } from '../models/section.model';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { CargarArticles } from '../interfaces/cargar-articles.interface';




const base_url=environment.base_url;
@Injectable({
  providedIn: 'root'
})
export class SectionService {

  constructor(private http:HttpClient){}
  get token(): string {
    return localStorage.getItem('token') || '';
  }

  get headers() {
    return {
      headers: {
        'x-token': this.token
      }
    }
  }

  cargarSections() {

    const url = `${ base_url }/section`;
    return this.http.get( url, this.headers )
              .pipe(
                map( (resp: {ok: boolean, sections: Section[] }) => resp.sections )
              );
  }
  

obtenerSectionsPorId(id:string) {
  const url = `${ base_url }/section/${id}`;
  return this.http.get( url, this.headers )
            .pipe(
              map( (resp: {ok: boolean,  sections: Section[] }) => resp.sections )
            );
}


crearSection(section:{title:string, description:string, date:Date ,autor:string}) {

  const url = `${ base_url }/section`;
  return this.http.post( url, section , this.headers );
}

actualizarSections( section:Section ) {

  const url = `${ base_url }/section/${ section._id }`;
  return this.http.put( url, section, this.headers );
}
borrarSections( _id: string ) {

  const url = `${ base_url }/section/${ _id }`;
  return this.http.delete( url, this.headers );
}
cargarArticlesPagination(page:number= 0) {
  const url = `${ base_url }/section?desde=5&page=${page}`;
  return this.http.get<CargarArticles>(url)
}
}













 




  
