import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { SchoolService } from '../../../services/school.service';
import { ActivatedRoute, Router } from '@angular/router';
import { SchoolModel } from '../../../models/school.model';
import Swal from 'sweetalert2';


@Component({
  selector: 'app-institucion',
  templateUrl: './institucion.component.html',
  styles: ['']
})
export class InstitucionComponent implements OnInit {
  public perfilForm: FormGroup;
  public schoolSelecionando: SchoolModel;

  public resultado=[
    {
      "_id":"1",
      "nombre":"Particular",
    },
    {
      "_id":"2",
      "nombre":"Fiscal",
    }
  ]
  constructor( private fb: FormBuilder,
              private schoolService:SchoolService,
              private activatedRoute:ActivatedRoute,
              private router:Router, ) {
  }

  ngOnInit(): void {

    this.activatedRoute.params.
      subscribe( ({id} ) => this.cargarSchool(id) );
    
    this.perfilForm=this.fb.group({
      nombre: [ '' , Validators.required ],
      email: [ 'uefarturoborja@admin.com', Validators.required  ],
      education: [ 'Fiscal' , Validators.required ],
      phone: [ "0958780392" , Validators.required ],
      date: [ "11/10/2003 " , Validators.required ],
      codeP: ["N3D52" , Validators.required ],
      country: [ "Ecuador - Quito", Validators.required ],
      street: [ "OEDF- S154 -Ciudadela Ibarra -La merced" , Validators.required ],
    });
  
}

 
  cargarSchool(id:string){
     this.schoolService.cargarSchoolById(id)
     .subscribe((school:any)=>{
       console.log(school);
      const {nombre,email, education, phone, date, codeP, country, street } = school;
       this.schoolSelecionando = school ;
       this.perfilForm.setValue({nombre,email, education, phone, date, codeP, country, street });
     })
    }
    guardarSchool() {

      const {nombre}=this.perfilForm.value;
        // actualizar
        const data = {
          ...this.perfilForm.value,
          _id: this.schoolSelecionando._id="60da30ded991fe468c3060af"
        }
        this.schoolService.actualizarSchool(data)
          .subscribe( resp => {
            Swal.fire('Actualizado', `${ nombre } actualizado correctamente`, 'success')
            this.router.navigateByUrl(`/dashboard`)
          })
      } 
    
}



