import { Component, OnInit } from '@angular/core';
import { Usuario } from 'src/app/models/usuario.model';
import { UsuarioService } from '../../services/usuario.service';
import { Router } from '@angular/router';
import { ItemsService } from '../../services/items.service';
import { NewsService } from '../../services/news.service';
import { SectionService } from '../../services/section.service';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styles: [
  ]
})
export class DashboardComponent {

  public usuario: Usuario;
  public usuarios: number = 0;
  public news: number = 0;
  public items: number = 0;
  public sections: number = 0;

  constructor( private usuarioService: UsuarioService,
                private itemService:ItemsService,
                private newsService:NewsService,
                private sectionService:SectionService,
               private router: Router ,) {
    
    this.usuario = usuarioService.usuario;
    this.cargarItems();
    this.cargarNoticia()
    this.cargarSection();
    this.cargarUsuarios();
  }
  public labels1: string[] = ['Mobile', 'Desktop', 'Tablet', 'Otros'];
  public data1 = [
    [40, 20, 10, 5],
  ];

  cargarItems(){
    this.itemService.cargarArticlesPagination()
    .subscribe( ({ total, items }) => {
      this.items = total;
       })
   }

   cargarSection(){
    this.sectionService.cargarArticlesPagination( )
      .subscribe( ({ total, sections }) => {
        this.sections = total;
    })
   }
   cargarUsuarios() {
    this.usuarioService.cargarUsuarios( )
      .subscribe( ({ total, usuarios }) => {
        this.usuarios = total;
    })
  }
   cargarNoticia() {
    this.newsService.cargarNoticePagination( )
      .subscribe( ({ total, news }) => {
        this.news = total;
    })
  }
  

}
